import { useState } from "react";
import { products } from "@/data/products";

interface FormData {
  name: string;
  company: string;
  email: string;
  phone: string;
  product: string;
  quantity: string;
  requirements: string;
}

export default function EnquiryPage() {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    company: "",
    email: "",
    phone: "",
    product: "",
    quantity: "",
    requirements: "",
  });

  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setMessage("");

    try {
      const response = await fetch("/api/send-email", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        setMessage("Email sent successfully!");
        setFormData({
          name: "",
          company: "",
          email: "",
          phone: "",
          product: "",
          quantity: "",
          requirements: "",
        });
      } else {
        setMessage("Failed to send email.");
      }
    } catch (error) {
      setMessage("Error sending email.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8 max-w-3xl">
      <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-6">
        Request Quote
      </h1>
      <p className="text-gray-600 mb-8">
        Please fill out the form below to request a quote for our products.
      </p>

      <div className="bg-white p-6 rounded-lg shadow-md">
        <form onSubmit={handleSubmit} className="space-y-6 bg-gray-100 p-6 rounded-lg shadow-md">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Name Input */}
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-gray-700">Name *</label>
              <input type="text" id="name" required className="mt-1 w-full p-2 border rounded-md" 
                value={formData.name} 
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              />
            </div>

            {/* Email Input */}
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700">Email *</label>
              <input type="email" id="email" required className="mt-1 w-full p-2 border rounded-md" 
                value={formData.email} 
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              />
            </div>

            {/* Phone Input */}
            <div>
              <label htmlFor="phone" className="block text-sm font-medium text-gray-700">Phone *</label>
              <input type="tel" id="phone" required className="mt-1 w-full p-2 border rounded-md" 
                value={formData.phone} 
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
              />
            </div>

            {/* Product Dropdown */}
            <div>
              <label htmlFor="product" className="block text-sm font-medium text-gray-700">Product *</label>
              <select id="product" required className="mt-1 w-full p-2 border rounded-md" 
                value={formData.product} 
                onChange={(e) => setFormData({ ...formData, product: e.target.value })}
              >
                <option value="">Select a product</option>
                {products.map((product) => (
                  <option key={product.id} value={product.id}>{product.name}</option>
                ))}
              </select>
            </div>
          </div>

          {/* Requirements Input */}
          <div>
            <label htmlFor="requirements" className="block text-sm font-medium text-gray-700">Specific Requirements</label>
            <textarea id="requirements" rows={4} className="mt-1 w-full p-2 border rounded-md" 
              value={formData.requirements} 
              onChange={(e) => setFormData({ ...formData, requirements: e.target.value })}
            ></textarea>
          </div>

          {/* Submit Button */}
          <button type="submit" className="w-full bg-[#0E9696] text-white py-3 px-4 rounded-md hover:bg-primary/90 transition-colors duration-300" disabled={loading}>
            {loading ? "Sending..." : "Submit Request"}
          </button>

          {/* Success/Error Message */}
          {message && <p className={`mt-4 text-center ${message.includes("successfully") ? "text-green-600" : "text-red-600"}`}>{message}</p>}
        </form>
      </div>
    </div>
  );
}
